:root {
  /* 色の設定 基本的にこの色しか使わない */
  --xcolor-black: #23263A;
  --xcolor-white: #F0EFF3;
  --xcolor-gray: #C7C2CE;
  --xcolor-blue: #454D7D;
  --xcolor-yellow: #CCFF00;

  /* 文字サイズ */
  --font-xlarge: 80px;
  --font-large: 32px;
  --font-medium: 24px;
  --font-small: 16px;

  /* その他設定 */
  --basic-margin: 100px;
  --heading-number-display: inline-block;
  --basic-align: left;
  --basic-flex-direction: row; 
    }
  @media (max-width: 767px) {
    :root {
      --font-xlarge: 60px;
      --basic-margin: 50px;
      --heading-number-display: none;
      --basic-align: center;
      --basic-flex-direction: row; 
    }
  }
  @media (max-width: 479px) {
    :root {
        /* 文字サイズ */
      --font-xlarge: 40px;
      --font-large: 24px;
      --font-medium: 16px;
      --font-small: 12px;
      --basic-margin: 20px;
      --heading-number-display: none;
      --basic-align: center;
      --basic-flex-direction: column; 
    }
  }

body{
  font-size: 24px;
  font-size: var(--font-medium);
}

a {
  display: inline-block;
  padding: .1rem;
}

a:hover, a:active, a:focus{
  background-color: #CCFF00;
  background-color: var(--xcolor-yellow);
  color: #23263A !important;
  color: var(--xcolor-black) !important; 
  -webkit-transition: 0.2s; 
  transition: 0.2s;
  text-decoration: none;
}

h1{
  font-size: 32px;
  font-size: var(--font-large);
  margin: 40px 0;
}

h2 {
  font-size: 32px;
  font-size: var(--font-large);
  color: #23263A;
  color: var(--xcolor-black);
  padding: 20px 0;
}

h3{
  font-size: 24px;
  font-size: var(--font-medium);
  color: #23263A;
  color: var(--xcolor-black);
  padding: 10px 0;
}

h4{
  font-size: 24px;
  font-size: var(--font-medium);
  color: #454D7D;
  color: var(--xcolor-blue);
  padding: 10px 0;
}

h5{
  font-size: 24px;
  font-size: var(--font-medium);
  color: #C7C2CE;
  color: var(--xcolor-gray);
  padding: 0;
}

img {
  margin: 20px;
  max-width: calc(100% - 40px);
}

p {
  line-height: 2.2rem;
  margin-bottom: 2rem;
}

blockquote {
  line-height: 2.2rem;
  margin-bottom: 3rem;
  border: 1px solid #CCFF00;
  border: 1px solid var(--xcolor-yellow);
  padding: 20px;
  margin: 20px 0;
  border-radius: 20px;

}

ul{
  margin: 1rem 4rem 4rem 4rem;
}

li {
  margin: 0.8rem;
}



ul.news{ 
  margin : 0;
}

.news li {
  margin: 0;
  list-style-type: none;
  border-top: #C7C2CE 1px solid;
  border-top: var(--xcolor-gray) 1px solid;
  border-bottom: #C7C2CE 1px solid;
  border-bottom: var(--xcolor-gray) 1px solid;
  padding: 20px 10px;
  font-size: 24px;
  font-size: var(--font-medium);
  text-align: left;
}

.news a {
  text-decoration: none;
}

.news .date:after{
  content: '>';
  color: #C7C2CE;
  color: var(--xcolor-gray);
  margin: 20px;
}

.news-box {
  background-color: #F0EFF3;
  background-color: var(--xcolor-white);
  border-radius: 8px;
  width: calc(50% - 100px /2);
  width: calc(50% - var(--basic-margin) /2);
  min-height: 30vh;
  height: 480px;
  padding: 8px;
  margin: 20px;
  overflow-y: scroll;
}

@media screen and (max-width:767px){
  .news-box{
    width: auto;
    height: auto;
  }
} 

.news-box a {
  width: 100%;
  text-decoration: none;
}

.news-box img {
  max-width: 100%;
  margin: 10px 0;
  border-radius: 20px;
}

