.layout-full {
  min-height: 100vh;
  width: 100vw;
  padding: 100px;
  padding: var(--basic-margin);
}

.layout-box{
  width: 100vw;
  max-width: 100vw;
  padding: calc(100px/2) 100px;
  padding: calc(var(--basic-margin)/2) var(--basic-margin) ;
}

.text-box {
  max-width: 640px;
  margin: auto;
}

.text-box p {
  line-height: 2.4rem;
  padding-bottom: var(--basic-margin/2);
  letter-spacing: 0.07em;
  text-align: justify;
  text-justify: inter-ideograph;
  text-indent: 1em;
}

.text-box ul {
  margin: 20px 0;
}

.text-box ul li {
  list-style: none;
  padding: 0;
  font-weight: 500;
  margin: 0;
}

.text-box img {
  padding: 20px 4px;   
  margin: 0;
}

.vertical-center{
  -webkit-align-self: center;
          align-self: center;
}

.grid-r-one{
  grid-template-rows: 1fr;
}

.grid{
  display: grid;
  grid-gap: calc(100px / 2 );
  gap: calc(100px / 2 );
  grid-gap: calc(var(--basic-margin) / 2 );
  gap: calc(var(--basic-margin) / 2 );
}

/* 見出し用 */

.main-heading {
  position: relative;
  text-align: left;
  margin-bottom: calc(32px *2);
  margin-bottom: calc(var(--font-large) *2);
}
.main-heading::before{
  content: '';
  display: block;
  position: relative;
  top: calc(80px *1.5);
  top: calc(var(--font-xlarge) *1.5);
  left: 0;
  width: calc(80px *1.5);
  width: calc(var(--font-xlarge) *1.5);
  height: 6px;
  background: #CCFF00;
  background: var(--xcolor-yellow);
}
.main-heading .number {
    font-size: 80px;
    font-size: var(--font-xlarge); 
    line-height: 80px; 
    line-height: var(--font-xlarge); 
    display: inline-block; 
    display: var(--heading-number-display);
    margin-right: 32px;
    width: calc(80px *1.5);
    width: calc(var(--font-xlarge) *1.5);
    text-align: center;
}
.main-heading .bun {
  font-size: 32px;
  font-size: var(--font-large);
  vertical-align: text-top;
}

/* photo-box */
.photo-box {
  margin: 20px 0;
  text-align: left;
  text-align: var(--basic-align);
}

.photo-box a {
  text-decoration: none;
  border : 1px solid transparent;
}

.photo-box a:hover {
  background: transparent;
  border: 1px solid #CCFF00;
  border: 1px solid var(--xcolor-yellow);
}


.photo-box img {
  width: 200px;
  height: 200px;
}
.photo-box h2 {
  color: #CCFF00;
  color: var(--xcolor-yellow);
  font-size: 80px;
  font-size: var(--font-xlarge);
}
.photo-box p {
  color: #C7C2CE;
  color: var(--xcolor-gray);
  font-size: 24px;
  font-size: var(--font-medium);
  overflow: wrap;
}

/*-box */
.round-box {
  margin: 20px;
  text-align: center;
}

.round-box a {
  text-decoration: none;
}


.round-box img {
  width: 256px;
  height: 256px;
  border: #C7C2CE 1px solid;
  border: var(--xcolor-gray) 1px solid;
  border-radius: 128px;
}
.round-box h2 {
  color: #23263A;
  color: var(--xcolor-black);
  font-size: 32px;
  font-size: var(--font-large);
  font-weight: bold;
}
.round-box p {
  color: #454D7D;
  color: var(--xcolor-blue);
  font-size: 24px;
  font-size: var(--font-medium);
  overflow: wrap;
}

.projects-box {
  margin: calc(100px / 2) 0;
  margin: calc(var(--basic-margin) / 2) 0;
}

