  border-bottom: var(--xcolor-gray) 1px solid;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

button, a.button {
  background: #23263A;
  background: var(--xcolor-black);
  color: #F0EFF3;
  color: var(--xcolor-white);
  text-align:center;
  min-width: 20vw;
  padding: .5rem;
  text-decoration: none;
  display: block;
  margin: 20px 0 ;
}
button:hover, button:active, button:focus ,a.button:hover, a.button:active, a.button:focus {
  background: #CCFF00;
  background: var(--xcolor-yellow);
  color: #454D7D;
  color: var(--xcolor-blue);
}

/* 文字周り */ 

.text-large{
  font-size: 32px;
  font-size: var(--font-large);
}

.text-small{
  font-size: 16px;
  font-size: var(--font-small)
}

.bold {
  font-weight: bold;
  letter-spacing: .1rem;
}

.shadow {
  text-shadow:  2px 2px 10px  #23263A;
  text-shadow:  2px 2px 10px  var(--xcolor-black);
}

.text-black {
  color: #23263A;
  color: var(--xcolor-black);
}

.text-gray {
  color: #C7C2CE;
  color: var(--xcolor-gray);
}

dl {
  margin: 40px 0;
  line-height: 2.2rem;
}

dt {
  font-weight: bold;
}
dd {
